.button {
    display: inline-flex;
    padding: 5px 24px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    border-radius: 3px;
    outline-style: none;
    border: none;
}

.disabled {

    opacity: 0.5;
    background-color: #004D99;

}

.secondary {

    border: 1px solid #06C;

}

.default {
    background-color:  #06C;

}

.red {
    background-color: red;
}

.default:hover {
    background-color: #0080FF;
}

.red:hover {
    background-color: #FF1A47;
}

.secondary:hover {
    background-color: #E5F0FA;
}


.default:active {
    background-color: #0059B2;
}

.secondary:active {
    background-color: #C9E5FF;
}

.red:active {
    background-color: #B21232;
}

.button:disabled {
    opacity: 0.5;
    background-color: #004D99;
}
